header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 61px;
  padding: 0 0.5rem;

}

#logo{
  font-size: 2.2rem;
  font-family:'MontSerrat';
  color:var(--primary-color);
  cursor:default

}

#sousHeader{
  display: flex;
  flex-direction: row;
  position:relative;
  height: calc(100vh - 61px);
  align-items: end;

}

.rond{
  height: 10px;
  width: 10px;
  background-color: black;
  border-radius: 100%;
}

.rond:nth-child(1){
  animation: descende 1s linear infinite

}

.rond:nth-child(2){
  animation: descende 1s linear infinite;
  animation-delay: 0.35s;
}

.rond:nth-child(3){
  animation: descende 1s linear infinite;
  animation-delay: 0.65s;
}

@keyframes descende {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-2px);
  }
  100% {
    transform: translateY(0);
  }
}

.blocPhoto{
  order: 1;
  /*height: 68vh;*/

  height: 100%;
  display: flex;
  justify-content: end;
  width: clamp(450px,63vw,600px);

}

.ancre{
  transition: transform 0.3s linear ;
  transform: translate3d(0,0,0);
cursor: pointer;
color: black;
text-decoration: none;
}

.ancre:hover{
  transform: translate3d(11%,0,0);

}

.blocTitre{
order:1;
display: flex;
flex-direction: column;
align-items: center;
height: 100%;
justify-content: space-between;
position: absolute;
width: max-content;
left:0
}

.blocTitre >:first-child{
  display:flex;
  flex-grow:1;
  align-items:center;
  margin-top:2vw
}

.blocTitre >:nth-child(2){
  margin-bottom: 2vw;
}

.blocTitre >:nth-child(2) > svg{
  width: 3vw;
}

.hiddenProjet{
  width:0;
  transform:scale3d(0,0,0)
}

.visibleProjet{
  width:auto;
 transition: transform 0.7s linear;
  transform:scale3d(1,1,1);
}

#image-canvas{


 height: 100%;
z-index: 2;
position: relative;


}

#containerMatrix{

  display: none;
}

#canvasMatrix{

  display: none;
}

#accueil{
  position: relative;
  height: 850px;


}

#sousAccueil{
  position: sticky;
  top:0;
  background-color: rgb(255, 255, 255);
  transition: transform 0.25s linear;
  overflow: hidden;
  will-change: transform;

}

#arrowBottom{
  animation: descend 1.2s ease-in-out infinite;
  fill:var(--primary-color);
  width: clamp(15%, 8vw, 22%);
  margin:auto;
  display: block;
  margin-top: 1rem;
}


@keyframes descend {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

#titreAccueil{
  font-size: clamp(2.4rem,10vw,4rem);
  padding-left: 5vw;
  color:var(--primary-color);
  cursor:default
}

#sectionModal{
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.blocModal{
  display: none;
}



.blocSvg{
  display: flex;
  flex-direction: column;
  gap:1.7rem;
  padding:0 1rem;
  cursor:default
}


#sectionProjets{
  color:white;
  overflow: hidden;

}

#titreProjet{
  font-size: 11vw;
  font-weight: bold;
  text-align: center;
  /*margin:7rem 0;*/

  font-family: 'Arcade';
  cursor:default
}

.projets{
  display: flex;
  justify-content: space-between;
  align-items: start;
  cursor:pointer;
  padding:1.7rem 0
}


.stars{
  opacity:0;
animation: opacityStars 1s linear;
}

@keyframes opacityStars {
  from{
    opacity:0
  }
  to{
    opacity: 1
  }
}

#blocProjets{
 padding: 0 1rem;
  margin: 6rem 0 6rem 0;
}

.sousTitreProjet{
  width: 50%;
}

.hoverProject{
  background-color: white;
}

.projets:hover + .hoverProject{

  background-color:#32C267;
}

.projets:hover{
  color:#32C267
}

.projets:hover  .techno{
  border:1px solid #32C267!important
}

.sousTitreProjet2{
  width: 50%;
  display: flex;
  flex-wrap: wrap;
  line-height: 1em;
  justify-content: end;
  align-items: start;
  gap:0.5rem
}

.sousTitreProjet > p{
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 2rem;

}

.sousTitreProjet > span{

  font-size: 0.95rem;
  color:lightslategrey;
  /*margin-bottom: 1.5rem;*/
  display: block;
}

.techno{
font-size: 1.1rem;
text-transform: uppercase;
border:0.1rem solid white;
border-radius: 1rem;
padding: 0.3rem
}

.projets:first-child{

/*padding-top:2rem*/
}

#moon{
  transform: perspective(300px) rotateX(42deg) translateY(80px);
  margin-left: 0.5rem;
  height: 16rem;
  /* Ajustez les valeurs selon vos besoins */
  width:100%;
  opacity:83%;
  filter: drop-shadow(0 -0.5rem 1.3rem rgb(255, 255, 255));
  position: absolute;
  bottom:0;
  left:0;
}

@keyframes spin {
  0% {
      transform: rotate(0deg);
  }
  100% {
      transform: rotate(360deg);
  }
}

.image-spin-infinite {
  animation: spin 170s linear infinite;
}

.inputContact{
  background:none;
  border:none;
  border-bottom: 1px solid white;
  color:white;
