  z-index: 5;
}

#containerBurger {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  clip-path: circle(0 at 100% 0);
  transition: clip-path 0.3s linear;
  background-color: var(--secondary-color);
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.expanded {
  clip-path: circle(100%)!important;

}

